<template>
  <section class="maintainDic">
    <div class="tree">
      <el-tree
        :data="data"
        :props="defaultProps"
        @node-click="handleNodeClick"
      ></el-tree>
    </div>
    <div class="content">
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true">
          <el-form-item>
            <el-button type="primary" @click="handleAdd">新增</el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
        <!-- <el-table-column type="selection" min-width="5%">
        </el-table-column> -->
        <el-table-column type="index" min-width="15%">
        </el-table-column>
        <el-table-column prop="param1" label="厂家" min-width="10%">
        </el-table-column>
        <el-table-column prop="param2" label="设备型号" min-width="10%">
        </el-table-column>
        <el-table-column prop="param3" label="注册日期" min-width="10%">
        </el-table-column>
        <el-table-column prop="param4" label="设备类型" min-width="10%">
        </el-table-column>
        <el-table-column prop="param5" label="序列号" min-width="10%">
        </el-table-column>
        <el-table-column prop="param6" label="安装位置" min-width="10%">
        </el-table-column>
        <el-table-column prop="param7" label="机构" min-width="10%">
        </el-table-column>
        <el-table-column prop="param8" label="IP" min-width="10%">
        </el-table-column>
        <el-table-column prop="param9" label="GIS" min-width="10%" sortable>
        </el-table-column>
        <el-table-column label="操作" min-width="20%">
          <template slot-scope="scope">
            <el-button size="small" @click="handleEdit(scope.$index, scope.row)">详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24" class="pagination">
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;" background>
        </el-pagination>
      </el-col>
    </div>
  </section>
</template>

<script>
//导入其他文件比如：组件/工具js/第三方插件js/json文件/图片等等import 《组件名称》 from '《组件路径》';
import util from '@/common/js/util'
import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser, getDeviceLists } from '@/api/api';

export default {
  //import引入的组件需要注入到对象中才能使用
  name: "maintainDic",
  props: {},
  components: {},
  data() {
    return {
      data: [
        {
          label: "设备厂家和型号",
          children: [
            {
              label: "设备厂家",
            },
            {
              label: "设备型号",
            }
          ]
        },
        {
          label: "设备类型管理",
          children: [
            {
              label: "智能手环",
            },
            {
              label: "智能床垫",
            },
            {
              label: "计数设备",
            },
            {
              label: "摄像头",
            },
            {
              label: "智能血压仪",
            },
            {
              label: "人脸识别设备",
            },
            {
              label: "指纹设备",
            },
            {
              label: "卡识别设备",
            }
          ]
        }
      ],
      defaultProps: {
        children: "children",
        label: "label"
      },

      filters: {
        name: ''
      },
      users: [],
      total: 0,
      page: 1,
      listLoading: false,
    };
  },
  beforeCreate() {
    //生命周期 - 创建之前
  },
  created() {},
  beforeMount() {}, //生命周期 - 挂载之前
  mounted() {},
  beforeUpdate() {}, //生命周期 - 更新之前
  computed: {
    //监听属性 类似于data概念
  },
  //方法集合
  methods: {
    handleNodeClick(data) {
      console.log(data);
    },

    handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取用户列表
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();getUserListPage
				getDeviceLists(para).then((res) => {
					this.total = res.data.total;
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
      },
      handleAdd() {

      }
  },
  watch: {
    //监控data中的数据变化
  },
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>

<style lang="scss" scoped>
//@import url(); 引入公共css类
.maintainDic {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  height: 100%;

  .tree {
    width: 15%;
    padding-top: 20px;
    
    >>> .el-tree-node + .el-tree-node {
      margin-top: 20px; 
    }
  }

  .content {
    width: 85%;
    border-left: 1px solid #cccccc;
    padding-top: 20px;
    padding-left: 20px;
  }
}
</style>
